body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ant-menu-item {
    font-size: 16px !important;
    margin-top: 40px !important;
    margin-bottom: 40px !important;
}
.ant-menu.ant-menu-dark .ant-menu-item-selected, .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
    font-size: 20px !important;
}
.anticon {
    font-size: 16px !important;
}
.ant-menu-item-selected i {
    font-size: 20px !important;
}

.logo {
    height: 25px;
    width: 131.944px;
    margin-top: 50px;
    margin-bottom: 50px;
    margin-left: 30px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    -moz-background-size: 100% 100%;
}
.layout {
    background-image: url(/static/media/bg.38d22613.png) !important;
    background-repeat: no-repeat !important;
    background-size: 100% 100% !important;
    -moz-background-size: 100% 100% !important; 
}
.ant-layout {
    background-color: rgba(255,255,255,0) !important;
}
.ant-layout-sider,.ant-layout-footer {
    background-color: #1a1a1a !important;
}
.ant-menu-dark, .ant-menu-dark .ant-menu-sub {
    background-color: #1a1a1a !important;
}
.ant-menu.ant-menu-dark .ant-menu-item-selected, .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected{
    background-color: rgba(255,255,255,0) !important; 
}
img {
    border: 0;
}
.steps-content {
    margin-top: 16px;
    /* border-radius: 20px; */
    /* background-color: rgba(40, 40, 40, 0.7) !important; */
    height: calc(100vh - 60px - 16px) !important;
    overflow-y: scroll;
    color: #fff;
    /* margin-left: 20px; */
  }
  
  .steps-action {
      background: #1a1a1a;
      height: 60px; 
  }
  .ant-steps {
      display: none !important;
  }

  .next-text {
      font-size: 18px;
      margin-left: 20px;
      color: #fff;
      cursor:pointer;
  }
  .prev-text {
    font-size: 18px;
    margin-right: 20px;
    color: #fff;
    cursor:pointer;
}
  .next-arrow {
      height: 20px;
      width: 20px;
      margin-right: 10px;
  }
  .prev-arrow {
    height: 20px;
    width: 20px;
}
  .submit-text {
      font-size: 18px;
      color: #c2bd0e;
      border: 1px solid #c2bd0e;
      padding: 5px;
      padding-left: 10px;
      padding-right: 10px;
      border-radius: 3px;
      margin-right: 30px;
      margin-left: 20px;
      background-color: rgba(194,189,14, 0.2) !important;
      cursor:pointer;
  }
  .split-text {
      color: #333;
  }
  .file-text {
    border-radius: 20px; 
    background-color: rgba(60, 60, 60, 0.6) !important;
    margin: 20px;
    padding: 10px 40px 40px 0;
    min-height: 100px;
  }
  .item-text,.item-date,.item-check {
      display: flex;
      margin-left: 50px;
      color: #9a9a9a;
      margin-top: 30px;
      font-size: 15px;
      
  }
  .item-file {
    margin-left: 50px;
    color: #9a9a9a;
    margin-top: 30px;
    font-size: 15px;
  }
  .item-text-name,.item-date-name,.item-check-name {
      text-align: center;
      margin-top: 5px;
  }
  .item-check-name {
      margin-left: 20px;
      margin-top: 0;
  }
  .item-text-value,.ant-input{
      flex: 1 1;
      margin-left: 20px;
      background-color: rgba(60, 60, 60, 0) !important;
      border: 0 !important;
      font-size: 18px;
      color: #fff;
  }
  .item-date-value{
    margin-left: 20px;
    background-color: rgba(60, 60, 60, 0) !important;
    border: 0 !important;
    font-size: 18px;
    color: #fff;
  }
  .ant-calendar-picker-icon {
      color: rgba(60, 60, 60, 0) !important;
  }
  .check-img {
      width: 26px;
      height: 26px;
  }
  .item-file-files {
      margin-top: 30px;
      height: 100px;
      overflow-x: auto;
      overflow-y: hidden;
      display: flex;
      text-align: center
  }
  a {
    text-decoration:none;
    color: #9a9a9a !important;
  }
  .item-file-files::-webkit-scrollbar {
    display: none;
  }
  .value-item {
      width: 80px;
      height: 80px;
      background-color: rgba(109, 108, 108, 0.4);
      margin-left: 20px;
      border-radius: 5px;
  }
  .file-icon{
      width: 30px;
      height: 30px;
      margin: 10px;
  }

@media screen and (max-width: 990px) {
    .item-file-files {
        width: calc(100vw - 140px) !important;
    }
}

@media screen and (min-width: 990px) {
    .item-file-files {
        width: calc(100vw - 340px) !important;
    }
}
  
.item {
    display: block;
    height: 30px;
    line-height: 30px;
    margin-top: 20px;
    color: #9a9a9a;
    font-size: 16px;
    margin-left: 20px;
}
.line {
}
.item-layout {
      margin-left: 30px;
      color: #fff;
      margin-top: 30px;
      font-size: 18px;
      overflow: scroll;
}
.item-title {
    margin-left: 20px;
    
}
.ant-radio-inner {
    width: 26px !important;
    height: 26px !important;
    border: 0 !important;
    margin-top: 2px;
    background: url(data:image/png;base64,)
}
.ant-radio-inner::after{
    top: 0 !important;
    left: 0 !important;
    width: 26px !important;
    height: 26px !important;
    border: 0 !important;
    background: url(data:image/png;base64,)
}
.item-value {
    line-height: 26px;
    height: 26px;
}
@media screen and (max-width: 990px) {
    .item-layout {
        width: calc(100vw - 140px) !important;
    }
}

@media screen and (min-width: 990px) {
    .item-layout {
        width: calc(100vw - 340px) !important;
    }
}
