.item {
    display: block;
    height: 30px;
    line-height: 30px;
    margin-top: 20px;
    color: #9a9a9a;
    font-size: 16px;
    margin-left: 20px;
}
.line {
}
.item-layout {
      margin-left: 30px;
      color: #fff;
      margin-top: 30px;
      font-size: 18px;
      overflow: scroll;
}
.item-title {
    margin-left: 20px;
    
}
.ant-radio-inner {
    width: 26px !important;
    height: 26px !important;
    border: 0 !important;
    margin-top: 2px;
    background: url('./images/btn_choose.png')
}
.ant-radio-inner::after{
    top: 0 !important;
    left: 0 !important;
    width: 26px !important;
    height: 26px !important;
    border: 0 !important;
    background: url('./images/btn_choose_pre.png')
}
.item-value {
    line-height: 26px;
    height: 26px;
}
@media screen and (max-width: 990px) {
    .item-layout {
        width: calc(100vw - 140px) !important;
    }
}

@media screen and (min-width: 990px) {
    .item-layout {
        width: calc(100vw - 340px) !important;
    }
}